import React, {useState,useEffect,useCallback}  from "react"
import { Link } from "gatsby"

const Header = () => {
  const [isMain,setIsMain]=useState(true)

  const hadleScroll = useCallback(()=>{
    const currentScrollPosition = window.pageYOffset
    const currenBrowserHeight = window.innerHeight
    if(currentScrollPosition>=currenBrowserHeight){
      setIsMain(false)
    }else{
      setIsMain(true)
    }
  },[])

  useEffect(()=>{
    if(window.location.pathname==='/'){
      window.addEventListener('scroll',hadleScroll)
      return()=>{
        window.removeEventListener('scroll',hadleScroll)
      }
    }
  },[hadleScroll])

  useEffect(()=>{
    if(window.location.pathname==='/'){
      setIsMain(true)
    }else{
      setIsMain(false)
    }
  },[])
  
  const headerStyle= isMain ? { padding:'5vh 5vw',width:'90vw',position:'absolute', top:'87vh', left:'0', transition: 'all .2s', color:'white',minWidth:'357px'}:{ padding:'5vh 5vw',width:'90vw',position: 'fixed',top:0,minWidth:'357px'}
  const imgStyle= isMain ? { filter: 'opacity(.6) drop-shadow(0 0 0 white)', position: 'absolute', width: '26px', height: '26px', color:'white',  backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)',backgroundSize: 'contain', backgroundRepeat: 'no-repeat'} : { position: 'absolute', width: '26px', height: '26px', backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' }
  const titleStyle = isMain ? { position: 'absolute', width: '90px', left: '30px', color:'white', fontSize:'23px',fontWeight:'600'} : { fontSize:'23px',fontWeight:'600',position: 'absolute', color:'black',width: '90px', left: '30px' }
  const linkStyle = isMain ? { float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'white'} :{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'black'}
  
  return(
    <header style={headerStyle}>
    <div>
      <Link to="/" style={{ position: 'relative',  textDecoration: `none`, }}>
        <div style={imgStyle}></div>
        <div className='header-title' style={titleStyle}>
          <span>Ho'</span>
          <span>space</span>
        </div>
      </Link>
    </div>
    <div style={{ width:'185px',margin: '0', padding: '0', listStyle: `none`, float: `right` }}>
      <li>      
        <Link className='header-about' style={linkStyle} to="/about/">About</Link>
      </li>
      <li>      
        <Link className='header-blog' style={linkStyle} to="/blog/">Blog</Link>
      </li>
      <li>      
        <Link className='header-contact' style={linkStyle} to="/contact/">Contact</Link>
      </li>
    </div>
  </header>
  )
}



export default Header
